.feedback-content {
  display: flex;
  flex-direction: row;
}
.feedback-left {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  width: 60%;
  background-color: @brand-color;
}
.feedback-left-blocks {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 126px 105px 200px 0;
  padding: 66px 101px 94px;
  max-width: 550px;
  border: 1px solid #cfcfcf;
}
.feedback-left-block-top {
  display: flex;
  align-items: end;
  justify-content: space-around;
}
.feedback-left-block-top-logo {
  border: 2px solid #fff;
  border-radius: 50px;
  max-width: 66px;
  height: 66px;
}
.feedback-left-block-top-info {
  width: 100%;
  margin-left: 24px;
}
.feedback-left-block-top-info-name {
  color: #fff;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
}
.feedback-left-block-top-info-location-img {
  margin-right: 9px;
  max-width: 12px;
}
.feedback-left-block-top-info-location {
  display: flex;
  align-items: center;
}
.feedback-left-block-top-info-location-position {
  color: #fff;
}
.feedback-left-block-bottom-descr {
  color: #fff;
}

// .arrow-next {
//   position: absolute;
//   top: 365px;
//   right: 60px;
//   max-width: 46px;
// }

.feedback-dots {
  position: absolute;
}

.feedback-right {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 10px 10px 50px;
  width: 40%;
  color: #fff;
}
// .feedback-left-title {
// }

@media screen and (max-width: 1145px) {
  .feedback-left-blocks {
    margin: 126px auto;
  }
}

@media @small-widths {
  .feedback-content {
    flex-direction: column-reverse;
  }
  .feedback-left {
    width: 100%;
  }
  .feedback-left-title {
    padding: 20px 0;
    font-size: 22px;
    line-height: 28px;
  }
  .feedback-right {
    width: 100%;
  }
}

@media screen and (max-width: 625px) {
  .feedback-left-blocks {
    padding: 20px 15px;
    margin: 20px auto 100px;
  }
}
