.benefits-content {
  display: flex;
  flex-direction: row;
}

.benefits-left {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  width: 40%;
}
.benefits-left-title {
  text-align: left;
  max-width: 450px;
}

.benefits-right {
  padding: 10px;
  width: 60%;
  color: #fff;
  background-color: @brand-color;
}

.benefits-right-text {
  max-width: 680px;
  padding: 30px 0 30px 117px;
}

.benefits-right-text-title {
  margin-top: 20px;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
}

.benefits-right-text :nth-child(1) {
  margin-top: 0;
}

.benefits-right-text-descr {
  font-size: 18px;
  line-height: 28px;
  color: #fff;
}

@media @small-widths {
  .benefits-content {
    flex-direction: column;
  }
  .benefits-left {
    padding: 20px 0;
    width: 100%;
  }
  .benefits-left-title {
    font-size: 22px;
    line-height: 28px;
    text-align: center;
  }
  .benefits-right {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .benefits-right-text {
    padding: 10px;
  }
  .benefits-right-text-title {
    font-size: 22px;
  }
}
