.why {
  padding: 40px 0;
  background-color: #f0f0f0;
}
.why-title {
  text-align: center;
}
.why-blocks {
  display: flex;
  align-items: baseline;
  justify-content: center;
  margin-top: 40px;
}
.why-block {
  width: 33%;
  margin: 0 35px;
}
.why-block-img {
  max-width: 118px;
}
.why-bock-title {
  margin: 37px 0 18px;
  font-size: 26px;
  line-height: 28px;
}
.why-block-descr {
  font-size: 18px;
  line-height: 28px;
}

@media @small-widths {
  .why {
    padding: 20px 0;
  }
  .why-title {
    font-size: 22px;
    line-height: 28px;
  }
  .why-blocks {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  .why-block {
    width: 100%;
  }
  .why-block-top {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
  }
  .why-bock-title {
    margin: 18px 0 18px;
    padding-left: 10px;
    font-size: 22px;
  }
}
