.steps {
  padding: 40px 0;
  background-color: #272727;
  color: #fff;
}
.steps-title {
  color: #fff;
  text-align: center;
}
.steps-items {
  display: flex;
  justify-content: center;
  margin: 40px 0;
}
.step-item-block {
  display: flex;
  align-items: baseline;
  width: 25%;
}
.step-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
  max-width: 185px;
}

.step-item-top {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 39px;
  padding: 23px 24px;
  border: 3px solid #c4c4c47a;
  border-radius: 70px;
}

.steps-line {
  margin: 0 30px;
  width: 170px;
  height: 1px;
  background-color: #c4c4c4;
}

.step-item-top-num {
  font-size: 46px;
  line-height: 46px;
}

.step-item-top-subnum {
  font-size: 32px;
  text-transform: uppercase;
}

.step-item-bottom {
  width: 100%;
  min-width: 185px;
  font-size: 18px;
  line-height: 28px;
}

.steps-btn-wrapper {
  display: flex;
  justify-content: center;
  padding: 10px;
}

@media @small-widths {
  .steps {
    padding: 20px 0;
  }
  .steps-title {
    padding-bottom: 20px;
    font-size: 22px;
    line-height: 28px;
  }
  .steps-items {
    flex-direction: column;
    align-items: center;
    margin: 10px 0;
  }
  .step-item-block {
    margin-bottom: 20px;
    justify-content: center;
    width: 100%;
  }
  .step-item-top {
    margin-bottom: 30px;
  }
  .steps-line {
    display: none;
  }
}
