.learning {
  background-color: #f0f0f0;
}

.learning-content {
  margin: 0 auto;
  padding: 40px 0;
  max-width: 795px;
  text-align: center;
}

.learning-title {
  padding-bottom: 15px;
}

.learning-descr {
  font-size: 26px;
  line-height: 38px;
}

@media @small-widths {
  .learning-content {
    padding: 20px 0;
  }
  .learning-title {
    padding-bottom: 10px;
    font-size: 22px;
    line-height: 28px;
  }
  .learning-descr {
    font-size: 16px;
    line-height: 24px;
  }
}
