.journey {
  background-color: #f0f0f0;
}

.journey-content {
  margin: 0 auto;
  padding: 40px 0;
  max-width: 795px;
  text-align: center;
}

.journey-title {
  padding-bottom: 15px;
}

.journey-descr {
  font-size: 26px;
  line-height: 38px;
}
.journey-btn-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  padding: 10px;
}

@media @small-widths {
  .journey-content {
    padding: 20px 0 10px;
  }
  .journey-title {
    padding-bottom: 10px;
    font-size: 22px;
    line-height: 28px;
  }
  .journey-descr {
    font-size: 16px;
    line-height: 24px;
  }
  .journey-btn-wrapper {
    margin-top: 10px;
  }
}
