a {
  text-decoration: none;
}

.btn {
  padding: 20px 167px;
  color: #fff;
  background-color: @brand-color;
  border-radius: 3px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  cursor: pointer;
}

.btn:hover {
  background-color: @hover-color;
}

@media @small-widths {
  .btn {
    padding: 20px 115px;
  }
}

@media @exxSmall-widths {
  .btn {
    padding: 20px 60px;
  }
}

