.header {
  font-family: "Gotham Pro", sans-serif;
}

.header-content {
  display: flex;
  flex-direction: row;
}

.header-left {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 70px 40px;
  width: 40%;
  background-color: #272727;
}
.logo-left {
  display: block;
  max-width: 210px;
  margin-bottom: 40px;
}
.header-left-content {
  display: block;
  width: 100%;
  max-width: 418px;
}

.logo-right {
  max-width: 210px;
}

.header-left-text {
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 36px;
}

.header-right {
  padding: 40px 0;
  width: 60%;
}

.logo-right {
  display: none;
}

.header-btn {
  display: none;
}

.header-right-title {
  padding: 0 20px 20px 50px;
  max-width: 1055px;
  font-size: 42px;
  line-height: 48px;
}

.video-wrapper {
  display: flex;
  justify-content: center;
  padding: 10px;
}

video {
  width: 100%;
  max-width: 815px;
}

@media @small-widths {
  .header-content {
    flex-direction: column-reverse;
    flex-wrap: wrap;
  }
  .header-left {
    justify-content: center;
    padding: 20px 10px;
    width: 100%;
  }
  .logo-left {
    display: none;
  }
  .header-left-text {
    text-align: center;
  }
  .header-right {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
  }
  .header-btn {
    display: block;
  }
  .header-right-title {
    padding: 20px 10px;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
  }
  .header-logo-right {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
  }
  .logo-right {
    display: block;
  }
  .video-wrapper {
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 625px) {
  .header-btn {
    padding: 10px 15px;
    font-size: 16px;
  }
}

@media screen and (max-width: 405px) {
  .logo-right {
    max-width: 175px;
  }
}

@media screen and (max-width: 370px) {
  .header-logo-right {
    flex-direction: column;
    align-items: center;
    padding: 0 20px;
  }
  .header-btn {
    margin-top: 15px;
  }
}
